import Axios from './index'
import axios from 'axios'

const cancelTokenSource = axios.CancelToken.source()
const cancelToken = cancelTokenSource.token

const abort = () => {
  cancelTokenSource.cancel()
}

const createContract = (title, pin, amount, symbol) =>
  Axios.post(
    '/trade-guard/contract/create',
    {
      title,
      pin,
      amount,
      symbol,
    },
    {
      cancelToken,
    }
  )

const getContracts = () => {
  return Axios.get('/trade-guard/contracts', {
    cancelToken,
  })
}

const redirectToContract = (pin) => {
  console.log(Axios.defaults.baseURL)
  // window.open(base + '/trade-guard/contract?pin=' + pin, '_blank')
}

export { createContract, getContracts, redirectToContract, abort }
