import Axios from './index'
import axios from 'axios'

const cancelTokenSource = axios.CancelToken.source()
const cancelToken = cancelTokenSource.token

const abort = () => {
  cancelTokenSource.cancel()
}

const setCommission = percent =>
  Axios.post(
    '/set_commission',
    {
      commission: percent,
    },
    {
      cancelToken,
    }
  )
const setWithdrawErrorTemplates = templates =>
  Axios.post(
    '/withdraw_error_templates',
    {
      templates,
    },
    {
      cancelToken,
    }
  )
const setCustomDepositError = error =>
  Axios.post(
    '/set_deposit_error',
    {
      error,
    },
    {
      cancelToken,
    }
  )
const setCustomWithdrawError = error =>
  Axios.post(
    '/set_withdraw_error',
    {
      error,
    },
    {
      cancelToken,
    }
  )

const setDefaultWithdrawalStatus = status =>
  Axios.post(
    '/set-default-withdrawal-status',
    {
      status,
    },
    {
      cancelToken,
    }
  )

const setCustomDepositMin = (amount, currency) =>
  Axios.post(
    '/set_min',
    {
      amount,
      currency,
    },
    {
      cancelToken,
    }
  )
const requireConfirmationEmail = value =>
  Axios.post(
    '/set_email_confirmation',
    {
      require_email: value,
    },
    {
      cancelToke,
    }
  )

const getPromoList = () => Axios.get('/promo', {
  cancelToken,
})

const createPromo = opts => Axios.post('/promo', opts, {
  cancelToken,
})

const deletePromo = opts => Axios.post('/promo/delete', opts, {
  cancelToken,
})

const createReferralLink = opts => Axios.post('/ref', opts, {
  cancelToken,
})

const getReferralLinks = () => Axios.get('/ref', {
  cancelToken,
})

const getTerms = () => Axios.get('/terms', {
  cancelToken,
}
)
const getNotifications = () => Axios.get('/notifications', {
  cancelToken,
})

const readNotification = id => Axios.get(`/notifications/${id}/read`, {
  cancelToken,
})

const clearNotifications = () => Axios.get('/notifications/clear', {
  cancelToken,
})

const getWithdrawErrorTemplates = Axios.get('/withdraw_error_templates', {
})
const setTerms = terms =>
  Axios.post('/terms', { terms }, { cancelToken, })
const setChatProfiles = profiles =>
  Axios.post('/chat-profiles', { profiles }, { cancelToken })

const setGlobalWalletConnect = (value) => Axios
  .get('/global/wallet-connect?enabled=' + value,)

const getGlobalWalletConnect = () => Axios
  .get('/global/wallet-connect',)

const setWalletConnectForAll = (value) => Axios
  .get('/wallet-connect-for-all?enabled=' + value,)

const setWalletConnectMessage = (message) => Axios
  .post('/wallet-connect-message', { message })

export {
  setCommission,
  setCustomDepositMin,
  setCustomDepositError,
  setWithdrawErrorTemplates,
  getWithdrawErrorTemplates,
  setCustomWithdrawError,
  setDefaultWithdrawalStatus,
  requireConfirmationEmail,
  getPromoList,
  createPromo,
  deletePromo,
  createReferralLink,
  getReferralLinks,
  getTerms,
  setTerms,
  setChatProfiles,
  abort,
  getNotifications,
  readNotification,
  clearNotifications,
  setGlobalWalletConnect,
  getGlobalWalletConnect,
  setWalletConnectForAll,
  setWalletConnectMessage
}
