import Axios from './index'
import axios from 'axios'

const cancelTokenSource = axios.CancelToken.source()
const cancelToken = cancelTokenSource.token

const abort = () => {
  cancelTokenSource.cancel()
}

const fetchMessages = (user, read) =>
  Axios.get(
    '/support?user=' + user + (read ? '&read=true' : ''),
    {
      cancelToken,
    }
  )
const sendMessage = (user, message) =>
  Axios.post(
    '/support/' + user + '/send',
    {
      message,
    },
    {
      cancelToken,
    }
  )

const sendGeneralMessage = (uid, text, user) =>
  Axios.post(
    '/general/' + uid + '/send',
    {
      text,
      user
    },
    {
      cancelToken,
    }
  )

const updateMessages = (messages, user) =>
  Axios.post(
    '/support/update',
    {
      messages,
      user,
    },
    {
      cancelToken,
    }
  )

export { fetchMessages, sendMessage, sendGeneralMessage, updateMessages, abort }
