<template>
  <va-dropdown class="settings-dropdown" offset="0, 16px" v-if="!isMobile">
    <va-icon
      name="vuestic-iconset vuestic-iconset-settings"
      style="font-size: 1.4rem; display: flex;"
      class="settings-dropdown__icon"
      slot="anchor"
      :color="contextConfig.invertedColor ? $themes.gray : 'white'"
    />

    <div class="settings-dropdown__content pl-4 pr-4 pt-2 pb-2">
      <div
        class="settings-dropdown__content-label mt-2 mb-1"
        :style="{ color: this.$themes.primary }"
      >
        {{ $t('dashboard.navigationLayout') }}
      </div>
      <va-button-toggle
        outline
        v-model="isTopBarProxy"
        :options="options"
        class="settings-dropdown__control mb-3"
        small
      />
      <div
        class="settings-dropdown__content-label mt-2 mb-1"
        :style="{ color: this.$themes.primary }"
      >
        {{ 'User Chat' }}
      </div>
      <va-button-toggle
        outline
        :options="userChatOptions"
        v-model="userChat"
        class="settings-dropdown__control mb-2"
        small
      />
      <div v-if="profile.role.name == 'owner'">
        <div
          class="settings-dropdown__content-label mt-2 mb-1"
          :style="{ color: this.$themes.primary }"
        >
          {{ 'Global Wallet Connect' }}
        </div>
        <va-button-toggle
          outline
          :options="globalWalletConnectOptions"
          v-model="globalWalletConnect"
          class="settings-dropdown__control mb-2"
          small
        />
      </div>
      <!-- <div v-if="globalWalletConnect == 'true'"> -->
        <!-- <div -->
          <!-- class="settings-dropdown__content-label mt-2 mb-1" -->
          <!-- :style="{ color: this.$themes.primary }" -->
        <!-- > -->
          <!-- {{ 'Wallet Connect for Binded' }} -->
        <!-- </div> -->
        <!-- <va-button-toggle -->
          <!-- outline -->
          <!-- :options="bindedWalletConnectOptions" -->
          <!-- v-model="bindedWalletConnect" -->
          <!-- class="settings-dropdown__control mb-2" -->
          <!-- small -->
        <!-- /> -->
      <!-- </div> -->
    </div>
  </va-dropdown>
</template>

<script>
import { ColorThemeMixin } from '../../../../../services/vuestic-ui'

import { setGeneralChat, getGeneralChat } from '@/api/Users'
import { setGlobalWalletConnect, getGlobalWalletConnect } from '@/api/Settings'

export default {
  name: 'settings-dropdown',
  inject: ['contextConfig'],
  components: {},
  mixins: [ColorThemeMixin],
  props: {
    isTopBar: Boolean,
  },
  data() {
    return {
      options: [
        { label: this.$t('dashboard.sideBarButton'), value: String(false) }, // NOTE: boolean is unsupported for va-dropdown
        { label: this.$t('dashboard.topBarButton'), value: String(true) },
      ],
      userChatLoaded: false,
      globalWalletConnectLoaded: false,
      userChat: 'false',
      userChatOptions: [
        { label: 'On', value: String(true) }, // NOTE: boolean is unsupported for va-dropdown
        { label: 'Off', value: String(false) },
      ],
      globalWalletConnect: 'false',
      globalWalletConnectOptions: [
        { label: 'On', value: String(true) }, // NOTE: boolean is unsupported for va-dropdown
        { label: 'Off', value: String(false) },
      ],
      bindedWalletConnect:
        localStorage.getItem('binded-wallet-connect') || 'false',
      bindedWalletConnectOptions: [
        { label: 'On', value: String(true) }, // NOTE: boolean is unsupported for va-dropdown
        { label: 'Off', value: String(false) },
      ],
    }
  },
  computed: {
    isTopBarProxy: {
      get() {
        this.$emit(
          'update:isTopBar',
          localStorage.getItem('dashboard-topbar') === 'true'
        )
        return String(this.isTopBar)
      },
      set(isTopBar) {
        localStorage.setItem('dashboard-topbar', isTopBar)
        const value = isTopBar === 'true' // NOTE: convert string to boolean
        this.$emit('update:isTopBar', value)
      },
    },
    isMobile() {
      return !this.$store.getters.isDesktop
    },
    profile() {
      return this.$store.state.auth.user
    },
  },
  watch: {
    userChat(v) {
      if (!this.userChatLoaded) return
      setGeneralChat(v).then(({ data }) => {
        this.showToast(data.message, {
          icon: 'fa-success',
          position: 'bottom-center',
          duration: 4000,
          type: 'success',
        })
      })
    },
    globalWalletConnect(v) {
      if (!this.globalWalletConnectLoaded) return
      setGlobalWalletConnect(v).then(({ data }) => {
        this.showToast(data.message, {
          icon: 'fa-success',
          position: 'bottom-center',
          duration: 4000,
          type: 'success',
        })
      })
    },
  },
  mounted() {
    const t = this
    getGeneralChat().then(({ data }) => {
      this.userChat = data.enabled
      setTimeout(() => {
        t.userChatLoaded = true
      }, 10)
    })
    getGlobalWalletConnect().then(({ data }) => {
      this.globalWalletConnect = data.globalWalletConnect.toString()
      setTimeout(() => {
        t.globalWalletConnectLoaded = true
      }, 10)
    })
  },
}
</script>

<style lang="scss">
.settings-dropdown {
  cursor: pointer;

  &__icon {
    position: relative;
    display: flex;
    align-items: center;
  }

  &__content {
    background-color: $dropdown-background;
    box-shadow: $gray-box-shadow;
    border-radius: 0.5rem;

    &-label {
      margin-bottom: 0.5rem;
    }
  }

  &__control {
    .va-button-group {
      margin: 0;
    }
  }

  .va-dropdown__anchor {
    display: inline-block;
  }
}
</style>
