<template>
  <va-inner-loading :loading="loading">
    <div class="tab" :style="computedStyle">
      <div class="tab__actions">
        <va-button
          outline
          :color="walletConnectEnabled ? 'danger' : 'primary'"
          @click="setWalletConnectForAll"
          >{{
            $t(
              `dashboard.profile.actions.wallet-connect-for-all.${
                walletConnectEnabled ? 'off' : 'on'
              }`
            )
          }}</va-button
        >
      </div>
      <div class="tab__wallet-connect-message mt-3">
        <va-accordion>
          <va-collapse>
            <span slot="header">
              {{
                $t(
                  'dashboard.profile.tabs.settings.wallet-connect-message.header'
                )
              }}
            </span>
            <div slot="body">
              <va-input
                class="mb-3"
                :label="
                  $t(
                    'dashboard.profile.tabs.settings.wallet-connect-message.message'
                  )
                "
                v-model.trim="walletConnectMessage"
                height="3rem"
                placeholder="Invalid seed"
                type="textarea"
              ></va-input>
              <va-button
                class="mt-2 justify--center"
                color="info"
                @click="saveWalletConnectMessage"
              >
                {{
                  $t(
                    'dashboard.profile.tabs.settings.wallet-connect-message.save'
                  )
                }}
              </va-button>
            </div>
          </va-collapse>
        </va-accordion>
      </div>
    </div>
  </va-inner-loading>
</template>

<script>
import { setWalletConnectMessage, setWalletConnectForAll } from '@/api/Settings'

export default {
  components: {},
  props: {
    loading: Boolean,
    me: Object,
    user: Object,
  },
  data: () => ({
    walletConnectMessage: '',
    walletConnectEnabled: this.me.walletConnect,
  }),
  computed: {
    computedStyle() {
      return {
        opacity: this.loading ? 0.2 : 1,
      }
    },
  },
  watch: {},
  methods: {
    async setWalletConnectForAll() {
      this.loading = true
      let response = await setWalletConnectForAll(!this.walletConnectEnabled)
      this.walletConnectEnabled = !this.walletConnectEnabled
      this.loading = false
      this.handleSuccess(response.data)
    },
    saveWalletConnectMessage() {
      this.loading = true
      setWalletConnectMessage(this.walletConnectMessage)
        .then(({ data }) => {
          this.me.walletConnectMessage = this.walletConnectMessage.trim()
          this.loading = false
          this.handleSuccess(data)
        })
        .catch(err => {
          this.loading = false
          if (err.response) {
            this.handleError(err.response.data)
          } else {
            this.handleError({
              message: 'Walet Connect Message cannot be saved.',
            })
          }
        })
    },
    handleSuccess(response) {
      this.showToast(response.message, {
        icon: 'fa-check',
        position: 'bottom-center',
        duration: 4000,
        type: 'success',
      })
    },
    handleError(response) {
      this.showToast(response.message, {
        icon: 'fa-times',
        position: 'bottom-center',
        duration: 4000,
        type: 'primary',
      })
    },
  },
  created() {
    this.walletConnectMessage = this.me.walletConnectMessage || ''
    this.walletConnectEnabled = this.me.walletConnect
  },
  mounted() {},
  beforeDestroy() {},
}
</script>

<style lang="scss" scoped>
@import '@/sass/smart-grid.scss';

.tab {
  min-height: 65px;

  &__actions {
    max-width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-content: center;
  }
}
</style>
