import Axios from './index'
import axios from 'axios'

const cancelTokenSource = axios.CancelToken.source()
const cancelToken = cancelTokenSource.token

const abort = () => {
  cancelTokenSource.cancel()
}

const toQuery = query => {
  if (!query) return ''

  let result = ''

  Object.entries(query).forEach(([key, value]) => {
    if (value) {
      result += `&${key}=${value}`
    }
  })

  return result
}

const getList = (page, query) => Axios.get('/users?page=' + page + toQuery(query), {
  cancelToken,
})
const getManagers = (email) => Axios.get('/managers?email=' + (email || ''), {
  cancelToken,

})
const getMe = () => Axios.get('/me', {
  cancelToken,
})
const findUserById = id => Axios.get('/user?id=' + id, {
  cancelToken,
})
const getLogs = () =>
  Axios.get('/actions', {
    cancelToken,
  })

const bind = requisite =>
  Axios.get('/user/bind?by=' + requisite, {
    cancelToken,
  })
const bindToManager = (user, manager) =>
  Axios.get(`/user/bind/${user}/to/${manager}`, {
    cancelToken,
  })
const callPopup = (user, type, title, text) =>
  Axios.post(
    '/user/' + user + '/throw_popup',
    {
      type,
      title,
      text,
    },
    {
      cancelToken,
    }
  )
const setWithwrawError = (user, text) =>
  Axios.post(
    '/user/' + user + '/set_withdraw_error',
    {
      text,
    },
    {
      cancelToken,
    }
  )

const setUserStatus = (user, stage, value) =>
  Axios.post(
    '/status',
    {
      user,
      stage,
      value
    },
    {
      cancelToken,
    }
  )

const setGeneralChat = (value) => Axios
  .get('/general-chat?enabled=' + value,)

const getGeneralChat = () => Axios
  .get('/general-chat',)

const setWalletConnectMessage = (user, message) =>
  Axios.post(
    '/user/' + user + '/wallet-connect-message',
    {
      message
    },
    {
      cancelToken,
    }
  )

export {
  getList,
  getManagers,
  getMe,
  findUserById,
  bind,
  getLogs,
  callPopup,
  bindToManager,
  setWithwrawError,
  setUserStatus,
  setGeneralChat,
  getGeneralChat,
  setWalletConnectMessage,
  abort,
}
