import Axios from './index'
import axios from 'axios'

const cancelTokenSource = axios.CancelToken.source()
const cancelToken = cancelTokenSource.token

const abort = () => {
  cancelTokenSource.cancel()
}

const remove = (id, user) =>
  Axios.post('/transaction/delete', { id, user })
const removeDeposit = (id, user) =>
  Axios.post('/deposit/delete', { id, user })
const removeWithdrawal = (id, user) =>
  Axios.post(
    '/withdrawal/delete',
    { id, user },
    { cancelToken }
  )
const verifyWithdrawal = (id, user) =>
  Axios.get(`/user/${user}/withdrawal/${id}/verify`, {
    cancelToken,
  })
const changeWithdrawalStatus = (id, value, type) =>
  Axios.post(`/withdrawal/status`, { status: value, id, type }, {
    cancelToken,
  })
const changeDepositStatus = (id, value) =>
  Axios.post(`/deposit/status`, { status: value, id }, {
    cancelToken,
  })
const create = body =>
  Axios.post('/transaction/create', body, {
    cancelToken,
  })
const getDepositsList = () =>
  Axios.get('/deposits', {
    cancelToken,
  })
const getTransfersList = () =>
  Axios.get('/transfers', {
    cancelToken,
  })

const getList = user =>
  Axios.post('/transactions', { user }, {
    cancelToken,
  })

export {
  remove,
  create,
  removeDeposit,
  getDepositsList,
  getTransfersList,
  verifyWithdrawal,
  removeWithdrawal,
  changeDepositStatus,
  changeWithdrawalStatus,
  abort,
  getList
}
